    width: 170px;
    height: 40px;

    background: #9b0a00;
    border-radius: 64px;
    border: none;

    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 29px;

    text-align: center;

    color: #ffffff;

    text-shadow: 0 0 2px rgba(0, 0, 0, 0.25);
    transition: 0.2s;
}

