        font-weight: 600;
        box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
        transition: 0.2s;
    }
}

@media screen and (max-width: 900px) {
    header .flex-row {
        flex-direction: column !important;
        align-items: flex-start;
    }

    header .header-content {
        flex-direction: column;
        transition: 0.2s;
    }

    header .logo-burger {
        width: 100%;
        flex-direction: row !important;
        align-items: center;
    }

    header .burger-container {
        width: 28px;
        height: 28px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    header .burger {
        position: relative;
        display: block;
        background: #1f1e16;
        width: 28px;
        height: 3px;
        box-shadow: 0 6px 0 #1f1e16, 0 -6px 0 #1f1e16;
        transition: 0.2s;
    }

    header .burger .x-line {
        background: #1f1e16;
        width: 28px;
        height: 3px;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        transform: rotate(-90deg);
        transition: 0.5s;
    }

    header a {
        margin: 5px 0;
    }

    header .localization {
        flex-direction: row !important;
        margin-top: 10px;
        margin-bottom: 10px;
        margin-left: 0;
    }
}

