.quiz-container {
    width: 100%;
    background: #ffffff;
    box-shadow: 0 0 8px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
}

.quiz-container .quiz-block {
    padding: 30px 0;
    box-sizing: border-box;
}

.quiz-container .thumbs-wrapper, .quiz-container .carousel-status {
    display: none;
}

.quiz-container .carousel .control-next.control-arrow::before {
    border-left: 15px solid #000 !important;
}

.quiz-container .carousel .control-prev.control-arrow::before {
    border-right: 15px solid #000 !important;
}

.quiz-container .carousel.carousel-slider .control-arrow:hover {
    background: none;
}

.quiz-container .carousel .control-arrow::before, .quiz-container .carousel.carousel-slider .control-arrow::before {
    border-top: 15px solid transparent;
    border-bottom: 15px solid transparent;
}

.quiz-container .carousel-root {
    width: 100%;
}

.quiz-container .carousel-root .slide {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20px 0;
    background: none;
}

.quiz-container h2 {
    padding: 0 100px;
    text-align: center;
}

.quiz-container .answers-container {
    display: grid;
    grid-gap: 40px;
    grid-template-columns: 1fr 1fr;
    margin-top: 50px;
}

.quiz-container .answers-container .answer {
    width: 320px;
    height: auto;
    padding: 10px;
    text-align: center;
    background: #f7c510;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.25);
    border-radius: 10px;

    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    line-height: 25px;

    display: flex;
    justify-content: center;
    align-items: center;

    color: #000000;
    cursor: pointer;
    transition: 0.2s;
}

.quiz-container .answers-container .answer:hover {
    transform: scale(1.05, 1.05);
    transition: 0.2s;
}

.quiz-container .question-description {
    margin: 50px 130px 0 130px;
    padding: 20px;
    background-color: #edf7ee;
    font-style: normal;
    font-weight: normal;
    font-size: 21px;
    line-height: 30px;

    text-align: center;

    color: #1f4721;
    -webkit-animation-name: show-it;
            animation-name: show-it;
    -webkit-animation-duration: 0.4s;
            animation-duration: 0.4s;
}

.quiz-container .question-wrong-description {
    margin: 50px 130px 0 130px;
    padding: 20px;
    background-color: #fff4e5;
    font-style: normal;
    font-weight: normal;
    font-size: 21px;
    line-height: 30px;

    text-align: center;

    color: #9b0a00;
    -webkit-animation-name: show-it;
            animation-name: show-it;
    -webkit-animation-duration: 0.4s;
            animation-duration: 0.4s;
}

@-webkit-keyframes show-it {
    from {
        transform: scale(0, 0);
    }
    to {
        transform: scale(1, 1);
    }
}

@keyframes show-it {
    from {
        transform: scale(0, 0);
    }
    to {
        transform: scale(1, 1);
    }
}

@media screen and (max-width: 900px) {
    .quiz-container h2 {
        width: 100%;
        padding: 0 40px;
        font-size: 25px;
    }

    .quiz-block {
        width: 100%;
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
    }

    .quiz-container .answers-container {
        grid-gap: 20px;
        grid-template-columns: 1fr;
        margin-top: 30px;
    }

    .quiz-container .answers-container .answer {
