    }

    .quiz-container .question-description {
        margin: 20px;
        padding: 15px 10px;
        font-size: 18px;
        line-height: 25px;
    }

    .quiz-container .question-wrong-description {
        margin: 20px;
        padding: 10px;
        font-size: 18px;
        line-height: 25px;
    }

    @-webkit-keyframes show-it {
        from {
            transform: scale(0, 0);
        }
        to {
            transform: scale(1, 1);
        }
    }

    @keyframes show-it {
        from {
            transform: scale(0, 0);
        }
        to {
            transform: scale(1, 1);
        }
    }
}

