.pre-post-section h1 {
    margin-bottom: 100px;

    font-style: normal;
    font-weight: 800;
    font-size: 60px;
    line-height: 60px;

    text-align: center;

    color: #1f1e16;

    text-shadow: 0 0 4px rgba(0, 0, 0, 0.25);
}

.pre-post-section label {
    margin-bottom: 17px;
    font-style: normal;
    font-weight: normal;
    font-size: 24px;
    line-height: 35px;
    font-feature-settings: "ss01" on;

    color: #1f1e16;
}

.pre-post-section input {
    margin-bottom: 25px;
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    line-height: 35px;
    font-feature-settings: "ss01" on;

    color: #1f1e16;
}

.pre-post-test-alternatives textarea {
    height: 50px;
}

.pre-post-section button {
    width: 280px;
    height: 60px;
    background: #007d7f;
    border: none;
    box-sizing: border-box;
    border-radius: 10px;

    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 35px;
    text-align: center;
    font-feature-settings: "ss01" on;

    color: #ffffff;
    transition: 0.2s;
}

.pre-post-section button:hover {
    transform: scale(1.02, 1.02);
    transition: 0.2s;
}

.pre-post-test-alternatives label, .pre-post-test-alternatives input {
    margin-bottom: 0 !important;
    margin-right: 10px;
}

.pre-post-test-alternatives {
    margin: 10px 0 10px 1%;
}

.pre-post-test-container button {
    margin-top: 20px;
}

@media screen and (max-width: 900px) {
    .coach-section h1 {
        line-height: 75px;
    }

    .pre-post-section h1 {
        font-size: 50px;
        line-height: 50px;
    }
}
